/* App.css */

/* 페이지 전체 스타일 */
body {
    font-family: Arial, sans-serif;
    background-color: #f0f0f0;
}

/* 헤더 스타일 */
h1 {
    color: #333;
    text-align: center;
}

/* 리스트 아이템 스타일 */
.player-list-item {
    border: 1px solid #ccc;
    margin: 5px;
    padding: 10px;
    background-color: #f5f5f5;
}

/* 리스트 아이템 내 텍스트 스타일 */
.player-list-item-heading {
    font-weight: bold;
}

/* 리스트 아이템 내 내용 스타일 */
.player-list-item-content {
    margin-left: 10px;
}
